.bg-primary {
  background-color: #E2A22F !important;
}

.text-primary {
  color: #E2A22F !important;
}

.text-secondary {
  color: white !important;
}

.bg-primary {
  background-color: #E2A22F !important;
}

.border-primary {
  border-color: #E2A22F !important;
}

body {
  line-height: 1.15 !important;
}

.btn:hover {
  background: #E2A22F !important;
  color: white !important;
  transition: all 0.3s ease-in-out;
}

.hero .fa, .hero .fas, .hero .fad {
  font-size: 5rem;
}

.skill .after {
  position: relative;
}
.skill .after::after {
  position: absolute;
  top: 0;
  right: 0;
  content: attr(name) "";
}
.skill .progress {
  background: none !important;
}
.skill .progress .progress-bar {
  background: #E2A22F;
}

.qualification .list-group-item {
  padding-left: 1.3rem;
}

.services .card {
  border-radius: 0;
  border-top-right-radius: 1.5rem;
  box-shadow: 0 0 4px white;
}
.services .card:hover {
  background: #E2A22F !important;
  color: white !important;
  transition: all 0.3s ease-in-out;
}
.services .card:hover i {
  background: white !important;
  color: #E2A22F;
}
.services .card .card-header {
  margin-top: -2.5rem;
  border: none;
}
.services .card i {
  border-radius: 0.5rem;
  box-shadow: 0 0 5px white;
}
@media (max-width: 768px) {
  .services .card {
    margin-bottom: 3rem;
  }
}

.testimonial .d-block {
  width: 10rem;
  height: 10rem;
}